@import url("https://fonts.googleapis.com/css?family=PT+Sans+Narrow:400,700|PT+Sans:400,700|Roboto+Condensed|Roboto:700");
/*VARIABLES*/

.header {
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  flex-wrap: wrap;
  max-width: 980px;
  margin: 0 auto;
  background: url("../assets/mwf.png") 225px 50px no-repeat, url("../assets/header-bg.png") 100%, no-repeat;
  background-size: contain, cover;
  /*____TIMER____*/
}

.header__deer:before {
  display: block;
  width: 44px;
  height: 50px;
  position: absolute;
  margin-top: 24px;
  content: "";
  background: no-repeat 0 0 url("../assets/sprite-icons.png");
}

.header__logo {
  display: block;
  width: 200px;
  height: 44px;
  margin: 27px 0 0 56px;
}

.header__features {
  display: block;
  width: 165px;
  margin-bottom: 10px;
  margin-left: 125px;
  color: #ededed;
  font-family: "PT Sans", sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 23.29px;
}

.header__features span {
  font-size: 24px;
  font-weight: 700;
}

.header__features.-natural:before {
  display: block;
  width: 29px;
  height: 37px;
  position: absolute;
  margin-top: -5px;
  margin-left: -34px;
  content: "";
  background: no-repeat, 0 -89px url("../assets/sprite-icons.png");
}

.header__features.-effective:before {
  display: block;
  width: 42px;
  height: 37px;
  position: absolute;
  margin-top: -7px;
  margin-left: -54px;
  content: "";
  background: no-repeat, 0 -51px url("../assets/sprite-icons.png");
}

.header__banner {
  display: block;
  width: 100%;
  text-align: center;
  margin-bottom: 20px;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-image: linear-gradient(to right, #f0e394 0%, #b49b52 100%);
  mix-blend-mode: normal;
  color: #ffd50c;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 60px;
  font-weight: 700;
  text-transform: uppercase;
}

.header__main-info {
  display: block;
  max-width: 335px;
}

.header__main-info.-sale {
  margin: 0 45px;
  padding-bottom: 10px;
}

.header__main-info.-sale:before {
  display: block;
  position: absolute;
  margin-top: -86px;
  margin-left: -5px;
  content: url("../assets/-50.png");
}

.header__info-banner {
  display: block;
  width: 327px;
  margin: 0;
  color: #ffffff;
  font-family: "Roboto", sans-serif;
  font-size: 22px;
  font-weight: 500;
}

.header__advantages-list {
  margin-top: 8px;
}

.header__advantages-item {
  display: block;
  width: 250px;
  color: #ffffff;
  font-family: "PT Sans", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 38px;
  list-style-type: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.7);
}

.header__advantages-item:before {
  display: block;
  width: 24px;
  height: 24px;
  position: absolute;
  margin-top: 8px;
  margin-left: -35px;
  content: "";
  background: no-repeat -31px -91px url("../assets/sprite-icons.png"), #C13814;
  border: 1px solid sandybrown;
  border-radius: 50%;
}

.header__advantages-item:last-child {
  display: block;
  width: 170px;
  margin-bottom: 20px;
  padding-top: 10px;
  line-height: normal;
}

.header__advantages-item:last-child {
  border-bottom: none;
}

.header__advantages-item:last-child:before {
  margin-top: -1px;
}

.header__product-box {
  display: block;
  position: absolute;
  z-index: 1;
}

.header__product-bottle {
  display: block;
  position: relative;
  top: 28px;
  left: 137px;
  z-index: 2;
}

.header__sale-info {
  display: block;
  width: 270px;
  color: #ffffff;
  font-family: "PT Sans Narrow", sans-serif;
  font-size: 19px;
  font-weight: 400;
  text-align: center;
  align-content: flex-end;
}

.header__order-container {
  display: block;
  align-self: flex-start;
  width: 290px;
  height: 500px;
}

.header__timer {
  display: inline-block;
  width: 290px;
}

.header__timer-value-container {
  display: inline-block;
  width: 46px;
  height: 52px;
  vertical-align: top;
  margin-top: 10px;
  box-shadow: 0 2px 3px rgba(27, 27, 27, 0.55), inset 0 1px 2px rgba(255, 255, 255, 0.15);
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 3px;
  background-color: transparent;
}

.header__timer-semisection {
  display: block;
  width: 46px;
  height: 25px;
  border-radius: 3px 3px 0 0;
  border-bottom: 1px solid rgba(206, 206, 206, 0.7);
  background-color: rgba(255, 255, 255, 0.5);
}

.header__timer-semisection:nth-child(3) {
  border-radius: 0 0 3px 3px;
  border-top: 1px solid rgba(206, 206, 206, 0.7);
}

.header__timer-semisection.-no-opacity {
  background-color: #ffffff;
}

.header__timer-value {
  display: block;
  position: absolute;
  margin-top: -6px;
  margin-left: 10px;
  opacity: 0.7;
  color: #000000;
  font-family: "PT Sans Narrow Bold", sans-serif;
  font-size: 50px;
  font-weight: 400;
  line-height: 11.93px;
}

.header__timer-value.-dark {
  opacity: 1;
}

.header__sale-quantity {
  display: inline-block;
  width: 120px;
  margin: 0;
  padding-left: 21px;
  white-space: pre;
  color: #ffffff;
  font-family: "PT Sans Narrow", sans-serif;
  font-size: 19px;
  font-weight: 400;
  line-height: 19px;
}

.header__sale {
  display: block;
  background: no-repeat 0 -11px/100% url("../assets/bg-old-new-price.png") #ffffff;
  box-shadow: 0 0 40px rgba(254, 213, 91, 0.25);
  border-radius: 10px;
}

.header__price-container {
  display: inline-block;
}

.header__price-container.-old {
  margin-top: 22px;
  margin-left: 25px;
}

.header__price-container.-new {
  padding-left: 26px;
}

.header__sale-type {
  display: block;
  color: #ffffff;
  font-family: "PT Sans Narrow", sans-serif;
  font-size: 18px;
  font-weight: 400;
}

.header__sale-type.-full-price {
  opacity: 0.3;
}

.header__full-coast {
  display: inline-block;
  margin: 0;
  opacity: 0.3;
  color: #ffffff;
  font-family: "PT Sans Narrow", sans-serif;
  font-size: 48px;
  font-weight: 400;
  text-decoration: line-through;
}

.header__full-coast.-rub {
  font-size: 24px;
  text-decoration: none;
}

.header__sale-coast {
  display: inline-block;
  margin: 0;
  color: #ffffff;
  font-family: "PT Sans Narrow Bold", sans-serif;
  font-weight: 400;
  font-size: 48px;
}

.header__sale-coast.-rub {
  font-size: 24px;
  text-decoration: none;
}

.header__form {
  display: block;
  margin-top: 40px;
}

.header__data-form {
  display: block;
  margin-bottom: 15px;
}

.header__data-form input::-webkit-input-placeholder,
.header__data-form input::-moz-placeholder,
.header__data-form input:-ms-input-placeholder {
  color: #929292;
  font-family: "PT Sans", sans-serif;
  font-size: 14px;
  font-weight: 400;
}

.header__input-field {
  display: block;
  width: 255px;
  margin: 0 auto;
  padding: 20px 15px;
  border-radius: 5px;
  border: 1px solid #e1e1e1;
  background-color: #f6f6f6;
  text-align: left;
}

.header__order-button {
  display: block;
  margin: 0 auto;
  padding: 17px 32px 16px 73px;
  box-shadow: 3px 4px 25px rgba(250, 74, 27, 0.35);
  border-radius: 5px;
  border: none;
  background-image: linear-gradient(to top, #d73f17 0%, #fa4a1b 100%);
  color: #ffffff;
  font-family: "PT Sans Narrow Bold", sans-serif;
  font-size: 22px;
  font-weight: 400;
  text-transform: uppercase;
}

.header__order-button:before {
  display: block;
  width: 51px;
  height: 60px;
  position: absolute;
  margin-top: -17px;
  margin-left: -73px;
  padding-top: 10px;
  background-color: #b13412;
  font-size: 28px;
  font-family: "PT Sans Narrow Bold", sans-serif;
  content: ">";
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}

.header__order-button:hover {
  cursor: pointer;
  transform: scale(1.1);
  transition: 0.8s;
  box-shadow: 0 6px 28px rgba(250, 32, 17, 0.67);
  background-image: linear-gradient(to top, #d73f17 50%, #fa280c 100%);
}

.header__safe-data {
  display: block;
  padding: 27px 0 24px 18px;
  text-align: center;
  color: #000000;
  font-family: "PT Sans Narrow", sans-serif;
  font-size: 15px;
  font-weight: 400;
}

.header__safe-data:before {
  display: block;
  width: 13px;
  height: 17px;
  position: absolute;
  margin-left: 7px;
  content: "";
  background: no-repeat -42px -48px url("../assets/sprite-icons.png");
}

@media all and (max-width: 1030px) {
  .header__deer {
    margin-left: 30px;
  }

  .header__banner {
    font-size: 38px;
  }
}

@media all and (max-width: 1005px) {
  .header {
    flex-direction: column;
    align-items: center;
  }

  .header__deer {
    margin-bottom: 10px;
    margin-left: 0;
  }

  .header__features {
    margin: 25px 0 0 20px;
  }

  .header__features.-natural:before {
    margin-left: -44px;
  }

  .header__main-info.-sale {
    margin: 110px 0 20px;
    padding-bottom: 10px;
  }

  .header__order-container {
    align-self: center;
  }
}

@media (max-width: 460px) {
  .header__banner {
    font-size: 22px;
  }

  .header__info-banner {
    font-size: 18px;
  }

  .header__main-info {
    margin-left: 30px;
  }

  .header__product-box {
    display: none;
  }

  .header__product-bottle {
    left: 20px;
  }
}

